<template>
    <div class="area">
        <div class="imgArea" :class="{activePositiv: selectPositives}" @click="toggleActive(true)">
            <div class="image" v-for="(n, i) in positives" :key="i">
                <img
                    :src="n.icon.src"
                    alt=""
                    @click="removePositives(i)"
                >
            </div>
        </div>
        <div class="imgArea" :class="{activeNegativ: !selectPositives}" @click="toggleActive(false)">
            <div class="image" v-for="(n, i) in negatives" :key="i">
                <img
                    :src="n.icon.src"
                    alt=""
                    @click="removeNegatives(i)"
                >
            </div>
        </div>
        <div :class="{loading}"><div :class="{'loading-wheel': loading}"></div></div>
        <div class="row">
            <div class="btn" @click="sendData">train</div>
            <div class="btn">stop</div>
        </div>
    </div>

</template>

<script>
export default {
    name: 'Svm',
    props: ['node', 'nodes', 'getNode'],
    data: () => ({
        positives: [],
        negatives: [],
        selectPositives: true,
        loading: false,
    }),
    watch: {
        node(n) {
            this.addNode(n);
        },
        nodes(nodes) {
            if (nodes) nodes.forEach(n => this.addNode(n));
        },
    },

    methods: {
        addNode(n) {
            if (n && this.positives.indexOf(n) === -1 && this.negatives.indexOf(n) === -1) {
                if (this.selectPositives) this.positives.push(n);
                else this.negatives.push(n);
            }
        },
        toggleActive(v) {
            this.selectPositives = v;
        },
        removePositives(i) {
            this.negatives.push(this.positives[i]);
            this.positives.splice(i, 1);
        },
        removeNegatives(i) {
            this.positives.push(this.negatives[i]);
            this.negatives.splice(i, 1);
        },
        async sendData() {
            this.loading = true
            console.log('send data clicked');
            const body = JSON.stringify({
                p: this.positives,
                n: this.negatives,
            });
            const data = await fetch('/api/v1/updateSvm', {
                method: 'POST',
                headers: { 'Content-type': 'application/json' },
                body,
            }).then(res => res.json()).catch(e => console.error(e));


            this.positives = [];
            data.p.forEach(i => this.positives.push(this.getNode(i)));
            this.negatives = [];
            data.n.forEach(i => this.negatives.push(this.getNode(i)));
            this.loading = false
        },
    },

};
</script>

<style scoped>
    .area {
        position: relative;

        padding: 0.1rem;
    }

    .loading {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.1);
    }

    .loading-wheel {
        width: 10px;
        height: 10px;
        margin-top: -20px;
        margin-left: -20px;

        position: absolute;
        top: 50%;
        left: 50%;

        border-width: 20px;
        border-radius: 50%;
        -webkit-animation: spin 1s linear infinite;

        border-style: double;
        border-color: #ccc transparent;
    }

    @-webkit-keyframes spin {
        0% {
            -webkit-transform: rotate(0);
        }
        100% {
            -webkit-transform: rotate(-360deg);
        }
    }

    .imgArea {
        min-height: 4rem;
        box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
        display: flex;
        align-items: center;
        flex-flow: wrap;
        flex-direction: row;
        margin-bottom: 1rem;
    }

    .activePositiv {
        box-shadow: 0 7px 14px rgba(10, 255, 0, 0.2), 0 3px 6px rgba(0,0,0,.2);
    }

    .activeNegativ {
        box-shadow: 0 7px 14px rgba(255, 0, 66, 0.2), 0 3px 6px rgba(0,0,0,.2);
    }

    img {
        height: 100%;
        width: 100%;
        object-fit: scale-down;
    }

    .image {
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-content: center;
        padding: 0.1rem;
    }
</style>



